$background-color: #f0f5f9;
$text-color: #333;
$header-bg-color: #333;
$button-blue: #0366d6;
$button-blue-hover: #0056b3;
$button-black: #000;
$button-black-hover: #333;

@mixin flexCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

body {
    font-family: Arial, sans-serif;
    background-color: $background-color;
    color: $text-color;
    background: url("https://cdnb.artstation.com/p/assets/images/images/061/950/865/large/naudaff3d-car-meet-1.jpg?1681996610");
    background-size: cover;
}

header {
    background-color: $header-bg-color;
    padding: 20px;
    @include flexCenter;
}

h1 {
    color: $button-blue;
    font-size: 3em;
    font-family: 'Overpass', sans-serif;
    margin-bottom: 20px;
}

#car-list {
    @include flexCenter;
    flex-wrap: wrap;
    gap: 20px;
    margin-top: 60px;
}

.car {
    width: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 10px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.car img {
    width: 100%;
    height: auto;
    border-radius: 5px;
}

.details-button-blue {
    background-color: $button-blue;
    &:hover { background-color: $button-blue-hover; }
}

.details-button-black {
    background-color: $button-black;
    &:hover { background-color: $button-black-hover; }
}

// Responsive design considerations
@media (max-width: 600px) {
    .car { width: 100%; }
    header, #car-list { flex-direction: column; }
}
